<template>
    <div class="body-box count-box">
        <div class="chart-box">
            <div class="count-menu">
                <span :class="{active: countIdx==0}" @click="selCount(0)"></span>
                <span :class="{active: countIdx==1}" @click="selCount(1)"></span>
            </div>
            <div class="chart-item">
              <div style="height:100%;width:100%;position: relative;">
                <iframe src="./chart/line.html" id="iframe" ref="iframe" @load="loaded" style="height:100%;" frameborder="no" border="0" scrolling="no" allowtransparency="yes"></iframe>
              </div>
            </div>
        </div>
        <div class="count-footer">
            <div class="select-count">
                <span :class="{fl:true,active: footerIdx==0}" @click="selFooterCount(0)"></span>
                <span :class="{fr:true,active: footerIdx==1}" @click="selFooterCount(1)"></span>
            </div>
        </div>
    </div>
</template>
<script>
    import {ProductHttp} from '@/api/productHttp';
    export default {
        name: 'dayCount',
        data(){
            return {
                countIdx:0,
                footerIdx:0,
                dataList:[],
                series:'',
                dataTypes:'day',
                timeout:0
            }
        },
        watch:{
            countIdx(newVal,oldVal){
                this.dataTypes = newVal?'month':'day';
                this.initChart();
            },
            footerIdx(newVal,oldVal){
                this.series = newVal?newVal:'';
                this.initChart();
            }
        },
        activated(){
           setTitle('');
           this.initChart();
        },
        methods:{
            selCount(idx){
                this.countIdx = idx;
            },
            selFooterCount(idx){
                this.footerIdx = idx;
            },
            loaded(){
                document.getElementById('iframe').style.width = '100%';
                let st = setInterval(()=>{
                   if(this.dataList.length>0){
                     clearInterval(st);
                     this.loadIframe();
                   }
                },100);
            },
            loadIframe(){
                if(!this.$refs.iframe.contentWindow.initChart) return;
                this.$refs.iframe.contentWindow.initChart();
            },
            initChart() {
                if(this.timeout) return;
                this.timeout = setTimeout(()=>{
                    clearTimeout(this.timeout);
                    this.timeout = 0;
                    sessionStorage.setItem('dataList','[]');
//                    ProductHttp.queryProdectCount({
//                        series:this.series,
//                        dataTypes:this.dataTypes
//                    }).then((res)=>{
                        let res = {"series":"1","dataTypes":"month","sDate":"201703","eDate":"201803","data":[{"code":"db","name":"","data":[{"open_date":"201707","scount":1238952.5,"show_date":";3/12","show_count":412984},{"open_date":"201708","scount":1178683.5,"show_date":";3/13","show_count":392894},{"open_date":"201709","scount":1273701.8,"show_date":";3/14","show_count":424567},{"open_date":"201710","scount":1143023.2,"show_date":";3/15","show_count":381007},{"open_date":"201711","scount":1530032.3,"show_date":";3/16","show_count":510010},{"open_date":"201712","scount":1623473.3,"show_date":";3/17","show_count":541157},{"open_date":"201801","scount":810051.6,"show_date":";3/18","show_count":270017}]},{"code":"cl","name":"","data":[{"open_date":"201707","scount":52171.6,"show_date":"07;2017","show_count":52171.6},{"open_date":"201708","scount":87078.6,"show_date":"08;2017","show_count":87078.6},{"open_date":"201709","scount":69612.8,"show_date":"09;2017","show_count":69612.8},{"open_date":"201710","scount":59120.4,"show_date":"10;2017","show_count":59120.4},{"open_date":"201711","scount":73553.4,"show_date":"11;2017","show_count":73553.4},{"open_date":"201712","scount":67013.8,"show_date":"12;2017","show_count":67013.8},{"open_date":"201801","scount":69966.1,"show_date":"01;2018","show_count":69966.1}]},{"code":"ck","name":"","data":[{"open_date":"201707","scount":71034,"show_date":"07;2017","show_count":71034},{"open_date":"201708","scount":61578.6,"show_date":"08;2017","show_count":61578.6},{"open_date":"201709","scount":75783.6,"show_date":"09;2017","show_count":75783.6},{"open_date":"201710","scount":63713.2,"show_date":"10;2017","show_count":63713.2},{"open_date":"201711","scount":74159.2,"show_date":"11;2017","show_count":74159.2},{"open_date":"201712","scount":54201.2,"show_date":"12;2017","show_count":54201.2},{"open_date":"201801","scount":99142.4,"show_date":"01;2018","show_count":99142.4}]}],"result":"","code":"1"};
                        if(res.code==1){
                            sessionStorage.setItem('dataList',JSON.stringify(res.data));
                            this.dataList = res.data;
                            this.loadIframe();
                        }
                    //})
                },100);
            }
        }
    }
</script>
<style lang="css" scoped>
    .count-box{
        height: 100vh;
        overflow: hidden;
        background-image: url("../../assets/img/coutn-bg.jpg");
        background-size: cover;
        background-position: center center;
    }
   .chart-box{
       width: 100%;
       height: 86vh;
       background-color: rgba(0,0,0,0.3);
       padding-top: 0.1rem;
   }
   .chart-item{
       height: 84%;
   }
   .count-footer{
       position: relative;
       margin-top: 0.25rem;
       height: calc(14vh - 0.25rem);
       box-sizing: border-box;
       background-color: rgba(0,0,0,0.3);
   }
   .count-menu{
       width: 160%;
       height: 1.6rem;
       line-height: 1.6rem;
       /*margin: 0 auto 0.5rem auto;*/
       margin-left: -30%;
       /*margin-bottom: 0.1rem;*/
       border: solid 1px #fff;
       border-radius: 10px;
       overflow: hidden;
       font-size: 0;
       transform: scale(.5);
   }
    .count-menu span{
         display: block;
         padding: 0;
         margin: 0;
         width: 50%;
         height: 100%;
         float: left;
         text-align: center;
         color: #fff;
         font-size: 32px;
     }
    .count-menu span.active{
        color: #3497f5;
        background-color: #ffffff;
        transition: all .3s;
    }
    .select-count{
        position: absolute;
        height: 1.6rem;
        line-height: 1.6rem;
        width: calc((100% - 0.5rem)*2);
        left:calc((0.5rem - 100%)/2 + 0.25rem);
        top: -0.05rem;
        transform: scale(.5);
    }
    @media only screen and (min-device-width :400px) and (-webkit-min-device-pixel-ratio : 3){
        .select-count{
            top: -9%;
        }
    }
    .select-count span{
        display: block;
        width: calc((100% - 0.25rem)/2);
        height: 100%;
        text-align: center;
        color: #fff;
        border: solid 1px #93afb3;
        box-sizing: border-box;
        border-radius: 10px;
        font-size: 32px;
    }
    .select-count span.active{
        background-color: #508791;
        border: solid 1px #508791;
        transition: all .3s;
    }
</style>



