<template lang="html">
    <div class="body-box scroll">
       <div class="carousel-box">
          <mt-swipe :show-indicators="false" :prevent="true" :auto="3000">
             <mt-swipe-item v-for="img in carouselImgs">
               <img class="img-item" :src="img">
             </mt-swipe-item>
          </mt-swipe>
          <div class="welcome">{{userInfo.name}}</div>
       </div>
       <div class="menu-box menu-tree">
           <div class="menu-nav" v-for="menu in menuList">
              <div class="title bd-bottom-1">
                <span>
                   <i class="mywork-icon" :style="{backgroundImage: 'url('+menu.resicon+')'}"></i>{{menu.resname}}
                </span>
              </div>
              <div class="menu-list clearfix">
                 <mt-cell :title="subMenu.resname"
                 v-for="subMenu in menu.submenus"
                 @click.native="goRouter(subMenu.resurl,subMenu.resid)" is-link>
                    <img slot="icon" :src="subMenu.resicon" height="18">
                 </mt-cell>
              </div>
           </div>
       </div>
    </div>
</template>
<script>
import {HomeHttp} from '@/api/homeHttp';
import { Swipe, SwipeItem,Badge,Indicator } from 'mint-ui';
export default {
    name:'kq',
    components:{Badge,Swipe,SwipeItem,Indicator},
    data(){
        return {
            carouselImgs:[],
            menuList:[],
            userInfo:{},
            screenInfo:{}
        }
    },
    activated(){
        setTitle(this.$route.query.title);
        this.screenInfo = {
          screenWidth:window.screen.width,
          screenHeight:window.screen.height,
          devicePixel:window.devicePixelRatio
        };
        this.userInfo = getUserInfo();
        this.queryMenuTree();
    },
    methods:{
        goRouter(url,resid){
            HomeHttp.saveLog({
                staff_num:this.userInfo.staff_num,
                res_id:resid
            });
            if(url.substring(0,1)=='/'){
                this.$router.push({
                    path:url
                });
            }else{
                window.location.href = url;
            }
        },
        queryMenuTree(){
            Indicator.open({
                text: '...',
                spinnerType: 'fading-circle'
            });
            let res = {"data":{"resid":"B3620000","pid":"B3600000","resname":"","resurl":"","resicon":"","numsurl":"","moreurl":"/home/kq","pics":"../lywx/images/banner2.png","sortnum":30,"shownum":4,"mob_link_type":1,"submenus":[{"resid":"B3620001","pid":"B3620000","resname":"","resurl":"","resicon":"../lywx/images/kaoqinguanli/personnel.png","numsurl":"","moreurl":"","pics":"","sortnum":"","shownum":4,"mob_link_type":1,"submenus":[{"resid":"B3620003","pid":"B3620001","resname":"","resurl":"/kq/exception","resicon":"../lywx/images/kaoqinguanli/unusual.png","numsurl":"","moreurl":"","pics":"","sortnum":3,"shownum":4,"mob_link_type":1},{"resid":"B3620002","pid":"B3620001","resname":"","resurl":"","resicon":"../lywx/images/kaoqinguanli/approve.png","numsurl":"","moreurl":"","pics":"","sortnum":"","shownum":4,"mob_link_type":1}]},{"resid":"B3620004","pid":"B3620000","resname":"","resurl":"","resicon":"../lywx/images/kaoqinguanli/query.png","numsurl":"","moreurl":"","pics":"","sortnum":"","shownum":4,"mob_link_type":1,"submenus":[{"resid":"B3620005","pid":"B3620004","resname":"","resurl":"/kq/leave","resicon":"../lywx/images/kaoqinguanli/leave.png","numsurl":"","moreurl":"","pics":"","sortnum":1,"shownum":4,"mob_link_type":1},{"resid":"B3620006","pid":"B3620004","resname":"","resurl":"/kq/applyQuery","resicon":"../lywx/images/kaoqinguanli/apply-for.png","numsurl":"","moreurl":"","pics":"","sortnum":2,"shownum":4,"mob_link_type":1}]},{"resid":"B3620007","pid":"B3620000","resname":"","resurl":"","resicon":"../lywx/images/kaoqinguanli/query.png","numsurl":"","moreurl":"","pics":"","sortnum":"","shownum":4,"mob_link_type":1,"submenus":[{"resid":"B3620009","pid":"B3620007","resname":"","resurl":"/kq/cardRecord","resicon":"../lywx/images/kaoqinguanli/card.png","numsurl":"","moreurl":"","pics":"","sortnum":4,"shownum":4,"mob_link_type":1},{"resid":"B3620008","pid":"B3620007","resname":"","resurl":"/kq/calendar","resicon":"../lywx/images/kaoqinguanli/attendance.png","numsurl":"","moreurl":"","pics":"","sortnum":"","shownum":4,"mob_link_type":1},{"resid":"B3620010","pid":"B3620007","resname":"","resurl":"/lywx/attendance/query_annual.html","resicon":"../lywx/images/kaoqinguanli/attendance-query.png","numsurl":"","moreurl":"","pics":"","sortnum":"","shownum":4,"mob_link_type":1},{"resid":"B3620011","pid":"B3620007","resname":"","resurl":"/kq/departKqQuery","resicon":"../lywx/images/kaoqinguanli/attendance-query.png","numsurl":"","moreurl":"","pics":"","sortnum":"","shownum":4,"mob_link_type":1},{"resid":"B3620012","pid":"B3620007","resname":"","resurl":"/kq/personKqQuery","resicon":"../lywx/images/kaoqinguanli/attendance-query.png","numsurl":"","moreurl":"","pics":"","sortnum":"","shownum":4,"mob_link_type":1}]}]},"result":"","code":"1"};
            this.menuList = res.data.submenus;
            this.carouselImgs = res.data.pics.split(';');
            this.$nextTick(()=>{
                Indicator.close();
            })
        }
    }
}
</script>
<style lang="css" scoped>
    .person-icon{
        background-image: url("../../assets/img/person-icon.png");
    }
    .leave-icon{
        background-image: url("../../assets/img/leave-icon.png");
    }
    .query-icon{
        background-image: url("../../assets/img/query-icon.png");
    }
    .menu-tree .menu-nav .title {
        line-height: 0.7rem;
        color: #999999;
    }
    .menu-list {
        padding: 0 0 0 0.4rem;
        overflow: hidden;
    }
</style>



